body{
    background: #e4e4e4;
    font-family: Arial, Helvetica, sans-serif;
}
.SL-header{
    display: flex;
    justify-content: space-between;
    height: 70px;
    padding: 10px 50px;
    background-color: white;
}
.SL-header>p{
    font-weight: bold;
    font-size: 16pt;
    color: grey;
    margin-right: 30px;
}

.imgLogo{
    width: 150px;
    height: 50px;
    margin-top: 10px;
}
.imgBg{
    width: 800px;
    padding: 40px 0 0 70px;
}

.boxContent{
    padding: 40px;
    
}
.boxContent>p{
    visibility: hidden;
    color: red;
    font-size: 11pt;
    font-weight: bold;
}

.box{
    width: 356px;
    position: absolute;
    right: 50px;
    background: white;
    top: 25%;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.loginItem>input{
    background: #e6e6e6;
    text-align: center;
    margin: 5px auto;
    border: 2px solid white;
    padding: 14px 10px;
    width: 250px;
    outline: none;
    color: #919191;
    font-size: 15px;
    border-radius: 24px;
    transition: 0.2s;

}
.loginItem>input:focus{
    transition: 0.5s;
    border: 2px solid red;
}

.btn{
    background: red;
    text-align: center;
    margin: 5px auto;
    padding: 10px 20px;
    width: 274px;
    height: 45px;
    color: white;
    font-size: 15px;
    border-radius: 24px;
    margin-top: 30px;
    font-weight: 700;
    transition: 0.25s;
    
}
.btn:hover{
    transition: 0.25s;
    background: #f5da0b;
    color: black;
    cursor: pointer;
}

.footerBox{
    width: 356px;
    height: 60px;
    border-radius: 0px 0px 10px 10px;
    background: #efefef;
    padding-top: 5px;
    font-size: 15px;
    color: #919191;
}
@media only screen and (max-width: 650px){
    .imgBg{
        visibility: hidden;
    }
    .box{
        position: static;
        margin: auto;
        margin-top: -450px;
    }
}