.feedbackHome{
    display: flex;
    padding: 10px 30px;
}
.feedbackContainer{
    width: 60%;
    background: white;
    margin: 30px 15px;
    text-align: center;
    border-radius: 10px;
    padding-bottom: 20px;
    box-shadow: 0 2px 8px 0 rgba(198, 198, 198, 0.2), 0 5px 10px 0 rgba(101, 101, 101, 0.19);
}

.feedbackForm{
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    flex-direction: column;
    padding-top: 30px;
}

.feedbackItem>input{
    background: #e6e6e6;
    text-align: left;
    margin-bottom: 20px;
    border: 2px solid white;
    padding: 14px 15px;
    width: 260px;
    outline: none;
    color: #696969;
    font-size: 15px;
    border-radius: 24px;
    width: 85%;
    
}

.feedbackItem>input:focus{
    transition: 0.5s;
    border: 2px solid red;
}

.feedbackInput{
    background: #e6e6e6;
    text-align: left;
    border: 2px solid white;
    padding: 14px 15px;
    width: 85%;
    outline: none;
    color: #696969;
    font-size: 15px;
    border-radius: 24px;
    resize: none;
    margin: auto;
}
.feedbackInput:focus{
    transition: 0.5s;
    border: 2px solid red;
}

#errMsg{
    visibility: hidden;
    color: red;
    font-size: 11pt;
    font-weight: bold;
}
.btnContainer{
    display: flex;
    width: 90%;
    margin: auto;
}
.btnReset{
    background: red;
    text-align: center;
    margin-right: 0;
    padding: 10px 20px;
    width: 50%;
    height: 20px;
    color: white;
    font-size: 15px;
    border-radius: 24px 0 0 24px;
    margin-top: 30px;
    font-weight: 700;
    
}
.btnSubmit{
    background: #5ed842;
    text-align: center;
    margin-left: 0;
    padding: 10px 20px;
    width: 50%;
    height: 20px;
    color: white;
    font-size: 15px;
    border-radius: 0 24px 24px 0;
    margin-top: 30px;
    font-weight: 700;
    
}
.btnSubmit:hover{
    transition: 0.25s;
    background: #45a031;
    cursor: pointer;
}
.btnReset:hover{
    transition: 0.25s;
    background: rgb(194, 0, 0);
    cursor: pointer;
}
.feedbackPic{
    display: table;
    width: 40%;
    height: 500px;
    background: #ffe9ab;
    margin: 30px 15px;
    border-radius: 10px;
    box-shadow: 0 2px 8px 0 rgba(198, 198, 198, 0.2), 0 5px 10px 0 rgba(101, 101, 101, 0.19);
}
.picWrap{
    display: table-cell;
    vertical-align: middle;
}
.fbPic{
    width: 100%;
}

@media only screen and (max-width: 650px){
    .feedbackHome{
        display: flex;
        flex-direction: column;
        padding-right: 20px;
    }
    .feedbackContainer{
        width: 90%;
    }
    .feedbackPic{
        width: 90%;
        margin-top: 0;
        height: 20%;
    }
    .picWrap{
        display: table-cell;
        vertical-align: bottom;
    }
}