.quotaContainerMyp{
    width: 90%;
    border-radius: 10px;
    padding-top: 15px;
    border: 3px solid rgb(227, 227, 227);
    margin: auto;
    height: 170px;
    box-shadow: 0 2px 8px 0 rgba(222, 222, 222, 0.2), 0 5px 10px 0 rgba(181, 181, 181, 0.19);
}
.packageInfo{
    display: flex;
    width: 90%;
    margin: auto;
    margin-top: 15px;
    justify-content: space-between;
}
.infoData{
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    width: 140px;
    height: 170px;
    border: 3px solid rgb(227, 227, 227);
    box-shadow: 0 2px 8px 0 rgba(222, 222, 222, 0.2), 0 5px 10px 0 rgba(181, 181, 181, 0.19);    

}

.infoData>h1{
    margin-bottom: 0px;
    text-align: left;
    margin-left: 15px;
    color: red;
}

.infoData>p{
    margin-top: 0;
    font-size: 12px;
    color: rgb(148, 148, 148);
    text-align: left;
    margin-left: 15px;
}

.infoData>h3{
    margin-bottom: 0;
    margin-top: 25px;
    margin-left: -5px;
}

.buybtn{
    width: 85%;
    height: 30px;
    border-radius: 6px;
    background: #ec3434;
    font-size: 13px;
    color: white;
    font-weight: 600;
    bottom: 20px;
    margin: auto;
    margin-top: 10px;
}
.buybtn>p{
    margin: 0;
}

@media only screen and (max-width: 650px){
    .packageInfo{
        flex-direction: column;
    }
    .infoData{
        width: 100%;
        margin: auto;
        margin-bottom: 20px;
    }
    .infoData>h1{
        text-align: center;
    }
    .infoData>p{
        text-align: center;
        margin-left: 0;
    }
}