.videoContainer{
    width: 95%;
    margin: auto;
    background: white;
    margin: 30px 15px;
    text-align: center;
    border-radius: 10px;
    padding-bottom: 20px;
    box-shadow: 0 2px 8px 0 rgba(198, 198, 198, 0.2), 0 5px 10px 0 rgba(101, 101, 101, 0.19);
}
.video{
    width: 60%;
    margin-top: 30px;
}

@media only screen and (max-width: 650px){
    .videoContainer{
        margin: auto;
        width: 90%;
        margin-top: 30px;
    }
    .video{
        width: 90%;
        margin-top: 20px;
    }
}