body{
    margin: 0;
}

.nav{
    border-bottom: 1px solid rgb(205, 205, 205);
    text-align: right;
    height: 70px;
    line-height: 70px;
    background-color: white;
    display: flex;
    justify-content: space-between;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.31);
}

.imgLogo{
    width: 150px;
    height: 50px;
    margin-top: 10px;
    margin-left: 30px;
}

.menu{
    margin: 0 30px 0 0;
}

.navText{
    clear: right;
    text-decoration: none;
    color: grey;
    font-weight: 600;
    margin: 0 15px;
    line-height: 70px;
}
.navText:hover{
    color: red;
    transition: 0.25s;
}

/* span{
    color: yellow;
} */

label{
    margin: 0 40px 0 0;
    font-size: 26px;
    line-height: 70px;
    display: none;
    width: 26px;
    float: right;
}
#toggle{
    display: none;
}

@media only screen and (max-width: 650px){
    .nav{
        display: block;
    }

    label{
        display: block;
        cursor: pointer;
    }
    .menu{
        text-align: center;
        width: 100%;
        display: none;
        margin-top: -18px;
    }
    .navText{
        display: block;
        border-bottom: 1px solid rgb(205, 205, 205);
        margin: 0;
        background-color: rgb(245, 245, 245);
    }
    #toggle:checked + .menu{
        display: block;
    }
}