.home{
    padding: 10px 30px;
    position: absolute;
    display: flex;
}
.profile{
    width: 35%;
    height: 500px;
    background: white;
    margin: 30px 15px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0 2px 8px 0 rgba(198, 198, 198, 0.2), 0 5px 10px 0 rgba(101, 101, 101, 0.19);
}
.profile>p{
    color: rgb(33, 33, 33);
    font-size: 14pt;
    margin-bottom: 0;
}
.dispNum{
    margin-top: 0;
    color: rgb(45, 45, 45);
    width: 100%;
    margin-bottom: 50px;
    border: 0px;
    text-align: center;
    font-size: 26px;
    font-weight: bold;
    background-color: transparent;
}

.infocontent{
    margin-top: auto;
    margin-bottom: auto;
}
.info{
    width: 65%;
    background: white;
    margin: 30px 15px;
    text-align: center;
    border-radius: 10px;
    padding-bottom: 20px;
    box-shadow: 0 2px 8px 0 rgba(198, 198, 198, 0.2), 0 5px 10px 0 rgba(101, 101, 101, 0.19);
}
.infoTxt{
    font-size: 24px;
    width: 180px;
    color: rgb(45, 45, 45);
    font-weight: 600;
    text-align: left;
    margin-bottom: -15px;
    margin-left: 60px;
}
.balTxt{
    color: rgb(45, 45, 45);
    font-size: 26px;
    width: 90%;
    border-radius: 10px;
    padding: 20px 0px;
    margin: auto;
    border: 3px solid rgb(227, 227, 227);
    box-shadow: 0 2px 8px 0 rgba(222, 222, 222, 0.2), 0 5px 10px 0 rgba(181, 181, 181, 0.19);
}
.headerInfo{
    border-radius: 10px 10px 0 0;
    background: #ec3434;
    height: 50px;
}
.headerInfo>p{
    margin: auto;
    font-size: 16px;
    padding-top: 15px;
    color: white;
    font-weight: 600;
}
.quotaContainer{
    width: 90%;
    border-radius: 10px;
    height: 200px;
    padding-top: 30px;
    border: 3px solid rgb(227, 227, 227);
    margin: auto;
    box-shadow: 0 2px 8px 0 rgba(222, 222, 222, 0.2), 0 5px 10px 0 rgba(181, 181, 181, 0.19);
}
/* for slider quotas */
.range{
	-webkit-appearance: none;
	width: 90%;
	height: 20px;
	background: #fee6a5;
    transition: 0.2s;
    border-radius: 5px;
    overflow: hidden;
}
/*ini buletannya*/
.range::-webkit-slider-thumb{
	-webkit-appearance: none;
    background: black;
    box-shadow: -210px 0 0 200px rgb(118, 118, 118);
	width: 20px;
	height: 20px;
	border-radius: 25%;
}
.dataContainer{
    margin-bottom: 20px;
}

.dataTxtContainerHome{
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: auto;
}
.dataTxtContainerHome>p{
    margin-top: 10px;
    margin-bottom: 5px;
    color: rgb(45, 45, 45);
    font-size: 18px;
}

.probg{
    width: 100%;
    border-radius: 10px 10px 0 0;
}
.dp{
    width: 120px;
    height: 120px;
    margin-top: -70px;
    border-radius: 50%;
}
.proEdit{
    width: 80%;
    height: 50px;
    border-radius: 10px;
    background: #ec3434;
    padding-top: 1px;
    font-size: 16px;
    color: white;
    font-weight: 600;
    bottom: 20px;
    margin: 50px auto;
}
#bolaPantul{
    width: auto;
}

@media only screen and (max-width: 650px){
    .home{
        display: flex;
        flex-direction: column;
        padding: 0;
    }
    .profile{
        width: 85%;
        margin: auto;
        margin-top: 30px;
    }
    .info{
        width: 85%;
        margin: auto;
        margin-top: 30px;
    }
    .proEdit{
        margin: auto;
    }
}